<template>
	<div class="row justify-content-center">
		<div class="col">
			<div class="card">
				<div class="card-header">
	               <h3 class="card-title"><div>Data Pasien & Dokter Pemeriksa</div></h3>
	                <div class="card-tools">
	                  <button type="button" class="btn btn-tool" data-card-widget="collapse" title="Collapse">
	                    <i class="fas fa-minus"></i>
	                  </button>
	                </div>
	              </div>
        		<div class="card-body ">
        			<div class="row px-lg-2">
        				<div class="col">
        					<div class="form-group">
			                  <label for="nama">Nama</label>
			                  <input type="text" class="form-control" id="nama" name="nama" disabled="" v-model="nama_pasien">
			                </div>
			                <div class="form-group">
			                  <label for="jenis_kelamin">Jenis Kelamin</label>
			                  <input type="text" class="form-control" id="jenis_kelamin" name="jenis_kelamin" disabled="" v-model="jenis_kelamin_pada_ui">
			                </div>
        				</div>
        				<div class="col">
        					<div class="form-group">
			                  <label for="umur">Umur</label>
			                  <input type="text" class="form-control" id="umur" name="umur" disabled="" v-model="umur">
			                </div>
        				</div>
        			</div>
        		</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { useRoute } from 'vue-router'
	import axios from 'axios'
	import { onMounted, ref } from 'vue'
	import get_age  from '@/umur.js'

	export default{
		components: {
		},
		setup(){
			const route = useRoute()
			const kunjungan_id = route.params.id
			const nomor_rekam_medis = ref()
			// Data Pasien
			const pasien_id = ref()
			const nama_pasien = ref()
			const jenis_kelamin = ref()
			const jenis_kelamin_pada_ui = ref()
			const umur = ref()


			const getRekamMedis = async () => {
				let response = await axios.get(`api/kunjungan/getWhereId/${kunjungan_id}`)
				let dataPasien = await axios.get(`api/pasien/getWhereId/${response.data.pasien_id}`)

				pasien_id.value = dataPasien.data.id
				nama_pasien.value = dataPasien.data.nama
				nomor_rekam_medis.value = dataPasien.data.nomor_rekam_medis
				jenis_kelamin.value = dataPasien.data.jenis_kelamin 
				if (dataPasien.data.jenis_kelamin == 'L') {
					jenis_kelamin_pada_ui.value = 'Laki-laki'
				} else {
					jenis_kelamin_pada_ui.value = 'Perempuan'
				}

				umur.value = dataPasien.data.tanggal_lahir
				umur.value = get_age(umur.value)
			}

			onMounted(() => {
				getRekamMedis()
			})

			return { kunjungan_id, nomor_rekam_medis, pasien_id, nama_pasien, jenis_kelamin, umur, get_age,jenis_kelamin_pada_ui }

		}
	}
</script>