<template>
	<div>
		<ContentHeader title="Kasir" url="/kasir" subTitle="Pembayaran" />
		<div class="content-header">
			<div class="container-fluid">
				<!-- Component -->
				<DataPasienDanDokter />
				<!-- End COmponent -->
				
				<div class="row justify-content-center">
					<div class="col">
						<div class="card">
							<div class="card-header">
								<h3 class="card-title"><div>Pembayaran</div></h3>
							</div>
							<form @submit.prevent="prosesPembayaran">
								<div class="card-body">
									<div class="row">
										<div class="col">
											<div class="form-group">
							                    <label for="dokter">Jasa Medis <span class="text-danger">*</span></label>
							                  	<select class="custom-select form-control" id="dokter" name="dokter" required="" @change="tambahAdministrasiKeTransaksi(administrasiModel)" v-model="administrasiModel">
		      										<option selected hidden=""></option>
			      									<option v-for="(row, index) in administrasi" :key="row.id" v-bind:value="index">{{ row.kode_administrasi }} - {{ row.nama }}</option>
			    								</select>
						                    </div>
										</div>
										<div class="col"></div>
									</div>
									<table class="table table-bordered table-hover">
									  <thead>
									    <tr>
									      <th scope="col" class="text-sm">NO</th>
									      <th scope="col" class="text-sm">NAMA TRANSAKSI</th>
									      <th scope="col" class="text-sm">JENIS</th>
									      <th scope="col" class="text-sm">TARIF</th>
									      <th scope="col" class="text-sm">ACTION</th>
									    </tr>
									  </thead>
									  <tbody>
									    <tr v-for="(row, index) in transaksi" :key="row.id">
									    	<td>{{ increment(index) }}</td>
									    	<td >{{ row.nama }}</td>
									    	<td >{{ row.jenis_administrasi }}</td>
									    	<td >Rp. {{ number_format(row.harga, 0, ',', '.') }}</td>
									    	<td><button class="btn btn-block btn-outline-danger btn-xs" @click="hapusDataTransaksi(row.id)" v-if="row.hapus">Hapus</button></td>
									    </tr>
									  </tbody>
									</table>
									<div class="row px-2 mt-3">
										<div class="col"></div>
										<div class="col">
											<div class="form-group">
												<label>Punya voucher?</label>
												<div class="row">
													<div class="col-8 d-flex">
														<input type="text" name="" placeholder="Masukkan kode voucher" class="form-control" v-model="voucher">
														<button class="btn btn-primary ml-2" @click="prosesVoucher">Gunakan</button>
													</div>
													<div class="col-4"></div>
												</div>
											</div>
											<div v-show="toggle">
												<p>Voucher senilai <span class="text-green">{{ format_nominal(nominalVoucher) }}</span></p>
												<p>Sub Total : <del>{{ subTotal }}</del></p>
											</div>
											<div v-show="toggleTidakTersedia">
												<p>Voucher Tidak Tersedia</p>
											</div>
											<label for="dokter">Total Pembayaran</label>
											<div style="background-color: #343A40;color: white;" class="p-2 h1 text-right">
												<span style="font-family: 'Averia Serif Libre', cursive;">Rp. {{ number_format(total, 0, ',', '.') }}</span>
											</div>
							                
							                <label for="metode_pembayaran" class="mt-4">Metode Pembayaran</label>
											<div class="form-group text-left mt-2">
			    								<div class="row align-items-center">
			    									<p class="col-6">Tunai:</p>
			    									<div class="col-6">
			    										<div class="input-group mb-3">
										                  <div class="input-group-prepend">
										                    <div type="button" class="btn btn-secondary">Rp.</div>
										                  </div>
										                  <!-- /btn-group -->
									                      <input type="number" class="form-control" v-model="transaksi_perawatan.tunai" @keyup="prosesKembalian">
										                </div>
			    									</div>
			    								</div>
			    								<div class="row align-items-center">
			    									<p class="col-3">Kartu Debit:</p>
			    									<select class="col-3 custom-select form-control mb-3 ml-2 mr-n2" id="metode_pembayaran_kartu_debit" name="metode_pembayaran_kartu_debit" v-model="transaksi_perawatan.metode_pembayaran_kartu_debit">
				      									<option selected="" hidden=""></option>
				      									<option value="bca">BCA</option>
				      									<option value="bni">BNI</option>
				      									<option value="bri">BRI</option>
				      									<option value="mandiri">MANDIRI</option>
				    								</select>
			    									<div class="col-6">
			    										<div class="input-group mb-3">
										                  <div class="input-group-prepend">
										                    <div type="button" class="btn btn-secondary">Rp.</div>
										                  </div>
										                  <!-- /btn-group -->
									                      <input type="number" class="form-control" v-model="transaksi_perawatan.kartu_debit" @keyup="prosesKembalian">
										                </div>
			    									</div>
			    								</div>
			    								<div class="row align-items-center">
			    									<p class="col-3">Kartu Kredit:</p>
			    									<select class="col-3 custom-select form-control mb-3 ml-2 mr-n2" id="metode_pembayaran_kartu_kredit" name="metode_pembayaran_kartu_kredit" v-model="transaksi_perawatan.metode_pembayaran_kartu_kredit">
				      									<option selected="" hidden=""></option>
				      									<option value="bca">BCA</option>
				      									<option value="bni">BNI</option>
				      									<option value="bri">BRI</option>
				      									<option value="mandiri">MANDIRI</option>
				    								</select>
			    									<div class="col-6">
			    										<div class="input-group mb-3">
										                  <div class="input-group-prepend">
										                    <div type="button" class="btn btn-secondary">Rp.</div>
										                  </div>
										                  <!-- /btn-group -->
									                      <input type="number" class="form-control" v-model="transaksi_perawatan.kartu_kredit" @keyup="prosesKembalian">
										                </div>
			    									</div>
			    								</div>
			    								<div class="row align-items-center">
			    									<p class="col-3">Transafer:</p>
			    									<select class="col-3 custom-select form-control mb-3 ml-2 mr-n2" id="metode_pembayaran_transfer" name="metode_pembayaran_transfer" v-model="transaksi_perawatan.metode_pembayaran_transfer">
				      									<option selected="" hidden=""></option>
				      									<option value="bca">BCA</option>
				      									<option value="bni">BNI</option>
				      									<option value="bri">BRI</option>
				      									<option value="mandiri">MANDIRI</option>
				    								</select>
			    									<div class="col-6">
			    										<div class="input-group mb-3">
										                  <div class="input-group-prepend">
										                    <div type="button" class="btn btn-secondary">Rp.</div>
										                  </div>
										                  <!-- /btn-group -->
									                      <input type="number" class="form-control" v-model="transaksi_perawatan.transfer" @keyup="prosesKembalian">
										                </div>
			    									</div>
			    								</div>
			    								<div class="row align-items-center">
			    									<p class="col-6">Biaya Lainnya:</p>
			    									<div class="col-6">
			    										<div class="input-group mb-3">
										                  <div class="input-group-prepend">
										                    <div type="button" class="btn btn-secondary">Rp.</div>
										                  </div>
										                  <!-- /btn-group -->
									                      <input type="number" class="form-control" v-model="transaksi_perawatan.biaya_lainnya" @keyup="prosesKembalian">
										                </div>
			    									</div>
			    								</div>
			    								<div class="row align-items-center">
			    									<p class="col-6">Kembalian:</p>
			    									<div class="col-6">
			    										<div class="input-group mb-3">
										                  <div class="input-group-prepend">
										                    <div type="button" class="btn btn-secondary">Rp.</div>
										                  </div>
										                  <!-- /btn-group -->
										                  <p class="form-control">{{ number_format(kembalian, 0, ',', '.') }}</p>
										                </div>
			    									</div>
			    								</div>
			    								<p class="text-danger ml-2" v-show="uangKurang">Uang yang diterima kurang</p>
			    								<div class="d-flex justify-centent-right mt-4">
			    									<button type="submit" class="btn btn-success d-flex align-items-center ml-auto">
													  <div>
													  	Proses Pembayaran
													  </div>	
													  <div>
									                    <svg v-if="loading" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style=" background: none; display: block; shape-rendering: auto;" width="200px" height="200px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
									                      <g>
									                        <path d="M50 15A35 35 0 1 0 74.74873734152916 25.251262658470843" fill="none" stroke="#ffffff" stroke-width="12"></path>
									                        <path d="M49 3L49 27L61 15L49 3" fill="#ffffff"></path>
									                        <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" values="0 50 50;360 50 50" keyTimes="0;1"></animateTransform>
									                      </g>
									                    </svg>
									                  </div>
													</button>
			    								</div>
						                    </div>
										</div>
									</div>
								</div>
							</form>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import ContentHeader from '@/components/ContentHeader2'
	import DataPasienDanDokter from '@/components/pelayanan/DataPasienDanDokter'
	
	import store from '@/store'
	import { computed, onMounted, ref, reactive } from 'vue'
	import axios from 'axios'
	import Swal from 'sweetalert2'
	import { useRoute } from 'vue-router'
	import router from '@/router'
	import number_format from '@/helper.js'
	import format_nominal from '@/format_nominal.js'

	export default{
		components: {
			ContentHeader: ContentHeader,
			DataPasienDanDokter: DataPasienDanDokter
		},
		setup(){
			const user = computed(() => store.getters['auth/user'])

			const { administrasi, administrasiModel, getAdministrasi } = useGetAdministrasi(user)
			const loading = ref(false)


			// ---------Tambah Data Administrasi Ke Transaksi---------
			const transaksi = ref([])
			const administrasi_id = ref([])

			function tambahAdministrasiKeTransaksi (id) {
				let data = administrasi.value[id]
				let fee_dokter = 0
				
				if(data.jenis_administrasi == 'administrasi') {
					fee_dokter = 0
				} else {
					fee_dokter = data.fee_dokter
				}

				transaksi.value.push({
					'id' : id,
					'nama' : data.nama,
					'jenis_administrasi' : data.jenis_administrasi,
					'harga' : data.harga,
					'fee_dokter' : fee_dokter,
					'fee_beautician' : 0,
					'point' : 0,
					'hapus' : true
				})

				administrasi_id.value.push(data.id)

				total.value = 0
				totalBayar()
			}

			const increment = (index) => {
				return index+1
			}
			
			// ---------End Tambah Data Administrasi Ke Transaksi---------

			// ---------Hapus Data Administrasi di Transaksi---------
			function hapusDataTransaksi (index) {

				for(let j = 0; j < transaksi.value.length; j++) {
					if (transaksi.value.[j].id == index) {
						transaksi.value.splice(j, 1)
					}
				}

				total.value = 0
				totalBayar()
			}
			// ---------End Hapus Data Administrasi di Transaksi---------


			// ---------Get data tindakan & resep obat---------
			const route = useRoute()
			const kunjungan_id = route.params.id
			const tindakan = ref()
			const tindakan_id = ref([])
			const resep_id = ref([])

			const getTindakanPasien = async () => {
				let response = await axios.get(`api/rekam_medis/getWhereIdKunjungan/${kunjungan_id}`)
				let data_tindakan = await axios.get(`api/tindakan/get/${user.value.cabang_id}`)
				let data_resep = await axios.get(`api/resep_obat/get/${user.value.cabang_id}`)		


				transaksi_perawatan.pasien_id = response.data.pasien_id 
				transaksi_perawatan.dokter_id = response.data.dokter_id

				for (var i = 0; i < data_tindakan.data.length; i++) {
					for (var j = 0; j < response.data.tindakan_id.length; j++) {
						if (data_tindakan.data[i].id == response.data.tindakan_id[j]) {
							transaksi.value.push({
								'id' : null,
								'nama' : data_tindakan.data[i].nama,
								'jenis_administrasi' : 'Perawatan',
								'harga' : data_tindakan.data[i].harga_pasien_member,
								'fee_dokter' : data_tindakan.data[i].fee_dokter,
								'beautician' : data_tindakan.data[i].beautician,
								'point' : data_tindakan.data[i].point,
								'hapus' : false,
							})

							tindakan_id.value.push(data_tindakan.data[i].id)

						}	
					}
				}
				
				for ( i = 0; i < data_resep.data.length; i++) {
					for ( j = 0; j < response.data.resep_id.length; j++) {
						if (data_resep.data[i].id == response.data.resep_id[j]) {
							transaksi.value.push({
								'id' : null,
								'nama' : data_resep.data[i].nama,
								'jenis_administrasi' : 'Resep Obat',
								'harga' : data_resep.data[i].harga,
								'fee_dokter' : data_resep.data[i].fee_resep,
								'beautician' : 0,
								'point' : 0,
								'hapus' : false,
							})

							resep_id.value.push(data_resep.data[i].id)
						}	
					}
				}
				
				totalBayar()
			}
			// ---------End Get data tindakan & resep obat---------


			// ---------Total Bayar, uand diterima & kembalian---------
			const total = ref(0)
			const uangDiterima = ref()
			const kembalian = ref(0)
			const uangKurang = ref(false)

			const totalBayar = () => {

				for (let k = 0; k < transaksi.value.length; k++) {
					total.value = total.value + transaksi.value.[k].harga
					transaksi_perawatan.total_bayar = total.value
				}
			}

			const prosesKembalian = () => {

				uangDiterima.value = parseInt(transaksi_perawatan.tunai) + parseInt(transaksi_perawatan.kartu_debit) + parseInt(transaksi_perawatan.kartu_kredit) + parseInt(transaksi_perawatan.transfer) + parseInt(transaksi_perawatan.biaya_lainnya)

				if (uangDiterima.value > total.value) {
					kembalian.value = Math.abs(total.value - uangDiterima.value)
				} else {
					kembalian.value = 0
				}
			}
			// ---------End Total Bayar, uand diterima & kembalian---------

			const { voucher, subTotal, nominalVoucher, toggle, toggleTidakTersedia, prosesVoucher } = useProsesVoucher(total)

			// Proses Pembayaran
			const transaksi_perawatan = reactive({
				"cabang_id" : user.value.cabang_id,
				"kunjungan_id" : kunjungan_id,
				"pasien_id" : "",
				"tindakan_id" : tindakan_id.value,
				"resep_id" : resep_id.value,
				"dokter_id" : "",
				"administrasi_id" : administrasi_id.value,
				"kode_voucher" : voucher.value,
				"total_fee_dokter" : 0,
				"total_point" : 0,
				"tunai" : 0,
				"kartu_debit": 0,
				"metode_pembayaran_kartu_debit" : "",
				"kartu_kredit": 0,
				"metode_pembayaran_kartu_kredit" : "",
				"transfer": 0,
				"metode_pembayaran_transfer" : "",
				"biaya_lainnya": 0,
				"total_bayar" : 0,
				"kembalian" : kembalian.value
			})


			const prosesPembayaran = () => {
				loading.value = true


				if (transaksi_perawatan.tunai == '') { transaksi_perawatan.tunai = 0 }
				if (transaksi_perawatan.kartu_debit == '') { transaksi_perawatan.kartu_debit = 0 }
				if (transaksi_perawatan.kartu_kredit == '') { transaksi_perawatan.kartu_kredit = 0 }
				if (transaksi_perawatan.transfer == '') { transaksi_perawatan.transfer = 0 }
				if (transaksi_perawatan.biaya_lainnya == '') { transaksi_perawatan.biaya_lainnya = 0 }

				let total_pembayaran = parseInt(transaksi_perawatan.tunai) + parseInt(transaksi_perawatan.kartu_debit) + parseInt(transaksi_perawatan.kartu_kredit) + parseInt(transaksi_perawatan.transfer) + parseInt(transaksi_perawatan.biaya_lainnya)


				if (total_pembayaran < total.value) {
					loading.value = false
					uangKurang.value = true

				} else {
					transaksi_perawatan.total_fee_dokter = transaksi.value.reduce((total, row) => {
						if (row.fee_dokter > 0 && row.fee_dokter <= 100) {
							return  total + (row.harga * row.fee_dokter) / 100
						} else {
							return total + row.fee_dokter
						}

					}, 0)


					transaksi_perawatan.total_point = transaksi.value.reduce((total, row) => {

						return total + row.point

					}, 0)

					uangKurang.value = false
					

					axios.post('api/transaksi_perawatan/create', transaksi_perawatan)
					.then((response) => {
						Swal.fire({
						  title: 'Berhasil!',
						  text: 'Berhasil menambahkan data',
						  icon: 'success',
						  confirmButtonText: 'Ok'
						})	
						loading.value = false
						console.log(response)
						router.push('/kasir')
					})
					.catch((error) => {
						Swal.fire({
						  title: 'Gagal!',
						  text: 'Gagal menambahkan data',
						  icon: 'error',
						  confirmButtonText: 'Ok'
						})	
						loading.value = false
						console.log(transaksi_perawatan)
						console.log(error)
					})
				}
			}
			// End Proses Pembayaran


			onMounted(() => {
				getAdministrasi()
				getTindakanPasien()
			})

			return { getAdministrasi, administrasi, tambahAdministrasiKeTransaksi, administrasiModel, transaksi, increment, hapusDataTransaksi, getTindakanPasien, tindakan, totalBayar, total, uangDiterima, kembalian, number_format, prosesKembalian, prosesPembayaran, voucher, prosesVoucher, subTotal, nominalVoucher, format_nominal, toggle, toggleTidakTersedia, uangKurang, transaksi_perawatan, loading }
		}

	}


	function useGetAdministrasi(user){
		// ---------Get Data Administrasi---------
		const administrasi = ref([])
		const administrasiModel = ref()
		const getAdministrasi = async () => {
			let response = await axios.get(`api/administrasi/get/${user.value.cabang_id}`)
			administrasi.value = response.data
		}

		// ---------End Get Data Administrasi---------

		return{
			administrasi, administrasiModel, getAdministrasi
		}
	}


	function useProsesVoucher (total) {
		const voucher = ref('')
		const subTotal = ref()
		const nominalVoucher = ref()
		const toggle = ref(false)
		const toggleTidakTersedia = ref(false)
		const totalSebelumnya = ref(0)

		const prosesVoucher = async () => {
			let response = await axios.get(`api/voucher/getWhereKodeVoucher/${voucher.value}`)
			
			if (response.data == 'kosong') {
				toggle.value = false
				toggleTidakTersedia.value = true	
				voucher.value = ''
			} else {
				toggle.value = true
				toggleTidakTersedia.value = false

				if (totalSebelumnya.value == 0) {
					totalSebelumnya.value = total.value
					nominalVoucher.value = response.data.nominal
					subTotal.value = total.value

					if (nominalVoucher.value <= 100) {
						let diskon = (total.value * nominalVoucher.value) / 100
						total.value = total.value - diskon
					} else {
						total.value = total.value - nominalVoucher.value
					}	
				} else {
					nominalVoucher.value = response.data.nominal
					subTotal.value = totalSebelumnya.value

					if (nominalVoucher.value <= 100) {
						let diskon = (totalSebelumnya.value * nominalVoucher.value) / 100
						total.value = totalSebelumnya.value - diskon
					} else {
						total.value = totalSebelumnya.value - nominalVoucher.value
					}
				}
			}
			
			
		}

		return {
			voucher,
			subTotal,
			nominalVoucher,
			toggle,
			toggleTidakTersedia,
			totalSebelumnya,
			prosesVoucher
		} 
	}

</script>

<style>
	@import url('https://fonts.googleapis.com/css2?family=Averia+Serif+Libre:wght@700&display=swap');
</style>